.card {
  height: 100%;
}
.card:hover {
  box-shadow: 0px 10px 13px -7px #000000;
}
.imageContainer {
overflow: hidden;
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.cardMedia {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform 0.3s ease-in-out;
}
.cardMedia:hover {
  transform: scale(1.05);
}
h3.cardTitle {
  font-size: 24px;
  line-height: 1.2;
  font-weight: 500;
  margin-bottom: 5px;
}
p.description {
  font-size: 20px;
  margin-bottom: 5px;
}
.cardActions {
  margin-top: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
